.header__container {
  grid-column: 1 / 4;
  grid-row: 1;
  position: relative;
  width: 100%;
  min-height: 100px;
  height: 100%;
  background: $primary-color-s;
  color: $text-color;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.8rem;
  text-align: center;
  box-shadow: 0px 5px 8px 1px rgba(0, 0, 0, 0.5);

}
