.footer {
  grid-column: 1/4;
  grid-row: 3;
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: $primary-color-s;
  color: $text-color;
  font-family: $big-title;
  font-weight: 300;
  font-size: 0.9rem;

  &__parrafo {
    font-size: 0.5rem;
    font-weight: 500;
  }
}
