.login {
  position: relative;
  display: flex;
  align-items: center;
  justify-self: center;
  margin-left: auto;
  margin-right: auto;
}

.login__container {
  width: 300px;
  height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.login__titulo {
  font-family: $big-title;
  font-weight: 900;
  font-size: 1.8rem;
}
.login__img {
  width: 150px;
  height: 150px;
}
.formulario {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
.formulario__label {
  font-size: 1.1rem;
  font-family: $big-title;
  font-weight: 300;
}
.login__input {
  height: 30px;
  width: 250px;
  color: $cta-color-l;
  background: rgba(251, 249, 249, 0.909);
  text-align: center;
  border: 1px solid rgb(220, 220, 220);
  border-radius: 15px;
  font-size: 1.2em;
  outline: none;
}
.recuperar {
  font-size: 0.6rem;
  color: $cta-color;
  text-align: center;
  font-family: $regular-title;
  font-weight: 500;
}
.login__input:hover {
  border: 1px solid rgb(157, 157, 157);
}
.formulario__btn{
  background: $cta-color-s;
  height: 30px;
  width: 50%;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
  color: $primary-color-s;
  box-shadow: 0px 5px 8px 1px rgba(0, 0, 0, 0.5);
  cursor: pointer;
 
}
.formulario__btn:hover{
  background: $primary-color-s;
  color: $text-color;
  transform: scale(1.1);

}
